}

.btn_remove {
  background-color: transparent;
  color: #444;
  border: 1px solid #dfdfdf;
}

.btn_remove:disabled {
  color: rgba(68, 68, 68, 0.54);
  background-color: transparent;
}

.btn_w100 {
  width: 100%;
}

.btn_white {
  background-color: #fff;
  color: #2471be;
  border: 1px solid #2471be;
}

.btn-icon {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background: none;
  width: 24px;
  height: 24px;
  padding: 3px;
  transition: color .3s;
}

.btn-icon:before {
  position: absolute;
  content: '';
  top: -6px;
  left: -6px;
  bottom: -6px;
  right: -6px;
}

.btn-icon .icon {
  display: block;
  font-size: 18px;
}

.btn-icon .icon-icons_close {
