.search-bar {
  position: relative;
  height: 100%;
}

.search-bar_hidden {
  position: absolute;
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s, visibility .3s;
}

.search-bar_hidden.is-active {
  opacity: 1;
  visibility: visible;
}

.search-bar.is-active {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
}

.search-bar__input {
  height: 100%;
}

.search-bar__btn {
  position: absolute;
  top: 50%;
  right: 16px;
  transform: translateY(-50%);
}

.search-bar__btn-search {
  display: block;
}

.search-bar.is-active .search-bar__btn-search {
  display: none;
}

.search-bar__btn-close {
  display: none;
}

.search-bar.is-active .search-bar__btn-close {
  display: block;
}

.search-bar__results {
  position: absolute;
  top: calc(100% + 1px);
  left: 0;
  right: 0;
  opacity: 0;
  visibility: hidden;
}

.search-bar.is-active .search-bar__results {
  opacity: 1;
  visibility: visible;
}

.search-bar__list {
  padding: 0 24px;
  background-color: #fff;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.25);
