.checkbox,
.radio {
  cursor: pointer;
  display: inline-block;
  padding: 2px 0;
}

.checkbox input,
.radio input {
  display: none;
}

.checkbox input:checked ~ .checkbox__text:before,
.checkbox input:checked ~ .radio__text:before,
.radio input:checked ~ .checkbox__text:before,
.radio input:checked ~ .radio__text:before {
  border-color: #f7941e;
}

.checkbox input:checked ~ .checkbox__text:after,
.checkbox input:checked ~ .radio__text:after,
.radio input:checked ~ .checkbox__text:after,
.radio input:checked ~ .radio__text:after {
  opacity: 1;
}

.checkbox__text,
.radio__text {
  position: relative;
  padding-left: 31px;
}

.checkbox__text:before,
.radio__text:before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 22px;
  height: 22px;
  background-color: #fff;
  border: 2px solid #c4c4c4;
  transition: border-color .3s;
}

.checkbox__text:after,
.radio__text:after {
  content: '';
  position: absolute;
  left: 6px;
  top: 50%;
  transform: translateY(-50%);
  width: 10px;
  height: 10px;
  background-color: #f7941e;
  opacity: 0;
  transition: opacity .3s;
}

.radio {
  padding: 3px 0;
}

.radio__text:before {
  width: 24px;
  height: 24px;
  border-radius: 50%;
}

.radio__text:after {
  border-radius: 50%;
  left: 7px;
}

