.page__content-wrap {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
}

.page__footer {
  flex: 0 0 auto;
}

.block {
  display: flex;
}

.header {
  height: 54px;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;
}

.header__inner {
  position: relative;
  height: 100%;
  background-color: #2471be;
  z-index: 10;
}

.header__wrap {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  max-width: 115px;
}

.header__logo img {
  display: block;
  width: 100%;
}

.header .icon-icons_close {
  display: none;
}

.header .icon-icons_menu {
  display: block;
}

.header.is-open-menu .icon-icons_close {
  display: block;
}

.header.is-open-menu .icon-icons_menu {
  display: none;
}

.main-menu {
  padding-top: 54px;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow: auto;
  background-color: #fff;
  z-index: -1;
  display: none;
}

.header.is-open-menu .main-menu {
  display: block;
}

.main-menu__wrap {
  padding: 24px 0;
  display: flex;
  flex-wrap: wrap;
  margin: 0 -8px;
  height: 100%;
}

.main-menu__item {
  width: 50%;
  min-height: 90px;
  padding: 4px 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-menu__item-content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  border: 1px solid #dfdfdf;
}

.main-menu__icon {
  width: 24px;
  height: 24px;
  color: #a7a7aa;
}

.main-menu__icon .icon {
  display: block;
  font-size: 24px;
}

.main-menu__name {
  font-size: 13px;
  margin-top: 10px;
}

.main-menu__footer {
  width: 100%;
  height: 50px;
  background-color: #f1f1f5;
}

.main-menu__footer-icon .icon {
  display: block;
  font-size: 21px;
}

.main-menu__footer-text {
  margin-left: 10px;
}

.main-menu__desktop {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.modal-header {
  height: 57px;
  border-bottom: 1px solid #dfdfdf;
}

.modal-header_low {
  height: 48px;
}

.modal-header__inner {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.head-object {
  height: 48px;
  border-bottom: 1px solid #dfdfdf;
  position: relative;
}

.head-object_high {
  height: 56px;
}

.head-object_bs {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.25);
  border: none;
}

.head-object__info {
  height: 100%;
  flex: 1 1 auto;
  display: flex;
  align-items: center;
}

.head-object__inner {
  height: 100%;
  display: flex;
  align-items: center;
}

.head-object__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  background: none;
  color: #999;
  font-size: 16px;
  margin-right: 10px;
}

.head-object__btn .icon {
  display: block;
}

.head-object__title {
  color: rgba(68, 68, 68, 0.54);
  margin-right: 16px;
}

.head-object__icon {
  margin-right: 17px;
  max-width: 10px;
}

.head-object__icon img {
  display: block;
  width: 100%;
}

.head-object__comment {
  margin-left: 8px;
  color: rgba(68, 68, 68, 0.54);
}

.head-object__btns {
  flex: 0 0 auto;
  display: flex;
  align-items: center;
}

.head-object__btns-item:not(:first-child) {
  margin-left: 24px;
}

.page-header {
  height: 57px;
  border-bottom: 1px solid #dfdfdf;
  background-color: #f3f3f3;
}

.page-header__wrap {
  position: relative;
  display: flex;
  align-items: center;
  height: 100%;
  padding-right: 24px;
}

@media only screen and (max-width: 359px) {
  .page-header__wrap {
    padding-right: 12px;
  }
}

.page-header__search {
  height: 100%;
  flex: 1 1 auto;
}

.page-header__search .form-control {
  height: 100%;
}

.page-header__btns {
  flex-shrink: 0;
  display: flex;
  align-items: center;
}

.page-header__btn {
  margin-left: 24px;
}

.tabs-header {
  height: 57px;
  border-bottom: 1px solid #dfdfdf;
}

.tabs-header__inner {
  display: flex;
  height: 100%;
}

.tabs-header__item {
  position: relative;
  flex: 1 1 72px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.tabs-header__item.is-active .btn-icon {
  color: #2471be;
}

.tabs-header__text {
  position: absolute;
  color: #2471be;
  font-size: 10px;
  font-weight: 600;
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s, visibility .3s;
}

.tabs-header__item.is-active .tabs-header__text {
  position: relative;
  opacity: 1;
  visibility: visible;
}

.objects-item {
  padding: 16px 0;
  border-bottom: 1px solid #dfdfdf;
}

.objects-item__top {
  display: flex;
  align-items: center;
}

.objects-item__img {
  max-width: 10px;
  margin-right: 19px;
  flex-shrink: 0;
}

.objects-item__img img {
  display: block;
  width: 100%;
}

.objects-item__info {
  flex: 1 0 auto;
  max-width: calc(100% - 29px);
}

.objects-item__title {
  font-weight: 600;
}

.objects-item__details {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.objects-item__location {
  padding-left: 18px;
  font-size: 12px;
  color: rgba(68, 68, 68, 0.54);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.objects-item__location span {
  white-space: nowrap;
}

.objects-item__date {
  font-size: 12px;
  color: rgba(68, 68, 68, 0.54);
  white-space: nowrap;
}

.objects-item__bottom {
  margin: 11px -1px 0;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.object-item-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px;
  background-color: #f3f3f3;
  min-width: 80px;
  height: 40px;
  flex: 1 0 30%;
  margin: 1px;
}

.object-item-btn__text {
  display: block;
  margin-left: 2px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.object-item-btn__icon {
  color: #a7a7aa;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.object-item-btn__icon .icon {
  display: block;
}

.object-item-btn.is-active .object-item-btn__icon_blue {
  color: #2471be;
}

.object-item-btn.is-active .object-item-btn__icon_green {
  color: #2eb525;
}

.object-page {
  padding-bottom: 24px;
}

.object-page__map {
  height: 200px;
  border-bottom: 1px solid #dfdfdf;
}

.object-page__content {
  padding-top: 16px;
}

.object-page__date, .object-page__location {
  font-size: 12px;
}

.object-page__location {
  margin-top: 5px;
}

.object-page__btns {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: 16px -1px 0;
}

.object-page__actions {
  display: flex;
  flex-wrap: wrap;
  margin: 16px -5px 0;
  align-items: center;
}

.object-page__actions-btn {
  width: 50%;
  padding: 0 5px;
}

.geozones-item {
  padding: 16px 0;
  border-bottom: 1px solid #dfdfdf;
}

.geozones-item__top {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.geozones-item__icon {
  margin-right: 16px;
  flex-shrink: 0;
  color: #a7a7aa;
  font-size: 16px;
}

.geozones-item__icon .icon {
  display: block;
  max-width: 100%;
}

.geozones-item__info {
  flex: 1 0 auto;
  max-width: calc(100% - 29px);
}

.geozones-item__title {
  font-weight: 600;
}

.geozones-item__details {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.geozones-item__location {
  font-size: 12px;
  color: rgba(68, 68, 68, 0.54);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.geozones-item__location span {
  white-space: nowrap;
}

.geozones-item__bottom {
  margin: 11px -1px 0;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.geozones-item-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px;
  background-color: #f3f3f3;
  height: 40px;
  flex: 1 0 calc(50% - 1px);
}

.geozones-item-btn:not(:first-child) {
  margin: 1px;
}

.geozones-item-btn__text {
  display: block;
  margin-left: 2px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.geozones-item-btn__icon {
  color: #a7a7aa;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.geozones-item-btn__icon .icon {
  display: block;
}

.geozones-item-btn__icon_crossed {
  position: relative;
}

.geozones-item-btn__icon_crossed:before, .geozones-item-btn__icon_crossed:after {
  content: '';
  position: absolute;
  height: 22px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
}

.geozones-item-btn__icon_crossed:before {
  width: 2px;
  background-color: #f90e0e;
  z-index: 5;
}

.geozones-item-btn__icon_crossed:after {
  width: 5px;
  background-color: #f3f3f3;
}

.object-item-btn.is-active .geozones-item-btn__icon_blue {
  color: #2471be;
}

.object-item-btn.is-active .geozones-item-btn__icon_green {
  color: #2eb525;
}

.geozone-type {
  height: 100%;
}

.geozone-type__inner {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.geozone-type__title {
  flex: 0 0 auto;
  padding-top: 24px;
}

.geozone-type__wrap {
  padding: 24px 0;
  flex: 1 0 auto;
  display: flex;
  flex-wrap: wrap;
}

.geozone-type__item {
  width: 100%;
  min-height: 90px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.geozone-type__item:not(:first-child) {
  margin-top: 8px;
}

.geozone-type__item-content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  border: 1px solid #dfdfdf;
}

.geozone-type__input {
  display: none;
}

.geozone-type__icon {
  width: 24px;
  height: 24px;
  color: #a7a7aa;
}

.geozone-type__icon .icon {
  display: block;
  font-size: 24px;
}

.geozone-type__name {
  font-size: 13px;
  margin-top: 10px;
}

.drop-selection {
  padding: 24px 0;
}

.drop-selection.is-open {
  opacity: 1;
  visibility: visible;
}

.drop-selection__input {
  display: none;
}

.drop-selection__item {
  display: flex;
  position: relative;
  align-items: center;
  padding-right: 15px;
  cursor: pointer;
}

.drop-selection__item:not(:first-child) {
  margin-top: 24px;
}

.drop-selection__icon {
  flex: 0 0 auto;
}

.drop-selection__text {
  flex: 1 0 auto;
  margin-left: 10px;
}

.drop-selection__check {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  color: #2471be;
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s, visibility .3s;
}

.drop-selection__input:checked ~ .drop-selection__check {
  opacity: 1;
  visibility: visible;
}

.head-drop {
  z-index: 10;
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  max-height: 50vh;
  border-top: 1px solid #dfdfdf;
  background-color: #fff;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.25);
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s, visibility .3s;
}

.head-drop_fullPage {
  height: calc(100vh - 56px - 54px);
  max-height: none;
}

.head-drop.is-open {
  opacity: 1;
  visibility: visible;
}

.head-drop__content {
  position: relative;
  flex: 1 0 auto;
}

.head-drop__content-wrap {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding: 24px 0 0;
  overflow: auto;
}

.head-drop__footer {
  flex: 0 0 auto;
}
