.header {
    height: 54px;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 100;
    &__inner {
        position: relative;
        height: 100%;
        background-color: $blue;
        z-index: 10; }
    &__wrap {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between; }
    &__button {}
    &__logo {
        max-width: 115px;
        img {
            display: block;
            width: 100%; } }
    .icon-icons_close {
        display: none; }
    .icon-icons_menu {
        display: block; }
    &.is-open-menu {
        .icon-icons_close {
            display: block; }
        .icon-icons_menu {
            display: none; } } }


.main-menu {
    padding-top: 54px;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow: auto;
    background-color: $white;
    z-index: -1;
    display: none;
    .header.is-open-menu & {
        display: block; }
    &__inner {
 }        // height: 100%
    &__wrap {
        padding: 24px 0;
        display: flex;
        flex-wrap: wrap;
        margin: 0 -8px;
        height: 100%; }
    &__item {
        width: 50%;
        min-height: 90px;
        padding: 4px 8px;
        display: flex;
        align-items: center;
        justify-content: center;
        &-content {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            width: 100%;
            height: 100%;
            border: 1px solid $lightGray; } }
    &__icon {
        width: 24px;
        height: 24px;
        color: $gray;
        .icon {
            display: block;
            font-size: 24px;
            // width: 100%
 } }            // height: 100%
    &__name {
        font-size: 13px;
        margin-top: 10px; }
    &__footer {
        width: 100%;
        height: 50px;
        background-color: $bgGray;
        // &-icon,
        // &-text
        //     display: inline-block
        &-icon {
            .icon {
                display: block;
                font-size: 21px; } }
        &-text {
            margin-left: 10px; } }
    &__desktop {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%; } }
